.main-nav {
  width: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
}

.main-nav__top {
  display: flex;
  justify-content: space-between;
  padding: 21px 30px;
  padding-bottom: 14px;
  border-bottom: 1px $border-gray solid;
}

.main-nav__deliver {
  font-family: $f-normal;
  font-size: 14px;
}

.main-nav__list {
  @include indents-zero();
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
}

@media (min-width: $tablet) {
  .main-nav__top {
    order: -1;
    border: none;
    padding: 20px 0 15px 0;
    margin-right: 90px;
  }

  .main-nav {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    height: 90px;
  }

  .main-nav__deliver {
    display: block;
    position: absolute;
    top: 90px;
    right: 0;
    z-index: 1;
    font-size: 15px;
  }
}

@media (min-width: $desktop) {
  .main-nav__top {
    margin-right: 0;
  }

  .main-nav__bottom {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}
